.about-menu {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: var(--pink);
    position: relative;
    align-items: center;
    padding: 0.5rem;
}

.about-menu::before {
    content: '';
    width: 100%;
    height: 100%;
    background-color: var(--white);
    position: absolute;
    animation: insetPath 2s forwards;
}

@keyframes insetPath {
    from {
        clip-path: inset(-55% -30% -45% -40%);
    }
    to {
        clip-path: inset(25% 5% 0% 5%);
    }
}

.header-appears {
    animation: headerAppears 1.5s 1.5s forwards;
}

@keyframes headerAppears {
    from {
        clip-path: polygon(100% -50%, 100% 15%, 93% 2%, 0 -50%);
        visibility: visible;
    }
    to {
        clip-path: polygon(100% -50%, 100% 100%, 0 100%, 0 -50%);
        visibility: visible;
    }
}

.about-menu-title-container {
    width: 100%; 
    max-width: 1000px;
}

.about-menu-header-title {
    text-align: center;
}
.about-menu-header {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    position: relative;
    max-width: 1000px;
    width: 100%;
    align-items: center;
}

.about-menu-text-box-width {
    width: 100%;
}

.about-description-texts-container {
    font-size: 5vmin;
}

.photo-container {
    width: 100%;
    height: 80vmin;
    border-radius: 1rem;
    background-color: black;
    padding: .1rem;
    object-fit: cover;
    object-position: center;
    max-width: 600px;
    box-shadow: 5px 5px 10px 1px black;
}

.photo {
    height: 100%;
    width: 100%;
    border-radius: 1rem;
}

.question {
    width: 100%;
    font-size: 5vmin;
    display: grid;
    grid-template-columns: .5fr 5fr .5fr;
}

.questionBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
    z-index: 1;
    position: relative;
    max-width: 1000px;
    width: 100%;
}

.questions {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.questionAnswer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0rem .5rem;
    gap: 1rem;
}

.answer {
    font-weight: bold;
    margin-left: 5vmin;
    display: flex;
    flex-wrap: wrap;
    font-size: 5vmin;
    position: relative;
}

.answer::before {
    content: 'â˜›';
    position: absolute;
    width: 5vmin;
    height: 5vmin;
    top: 0;
    left: -5vmin;
}
.question-answer-btn-group {
    display: flex;
    justify-content: flex-end;
}

.packMan-div-menu-about {
    position: relative;
    top: 0;
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 15vmin;
}

.packMan-line-menu-about {
    width: 100%;
    top: 18vmin;
    height: 1vmin;
    position: absolute;
    background-color: violet;
    opacity: .5;
}

.question-box-animation-container {
    width: 100%;
    background-color: red;
}

.lockIcon {
    width: 7vmin;
    height: 7vmin;
}

.lockIcon:hover {
    transform: scale(1.1);
}